import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink
} from 'reactstrap';

const Header = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
      <Navbar className='header' expand="md">
        <NavbarBrand href="/portfolio" className='name-logo'>Stephen Araka</NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="links ml-auto" navbar>
            <NavItem>
              <NavLink href="/portfolio/about-me">About Me</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/portfolio/projects">Projects</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/portfolio/resume">Resume</NavLink>
            </NavItem>
            <NavItem>
              <NavLink href="/portfolio/contact">Contact</NavLink>
            </NavItem>
          </Nav>
        </Collapse>
      </Navbar>
    </div>
  );
}

export default Header;